












import {Component, Prop, Vue} from 'vue-property-decorator';
import {Site, Theme} from "@/interfaces/appClass";

@Component({
  name: 'top-header'
})
export default class TopHeader extends Vue {
  @Prop({
    required: true,
    type: Object
  })
  site: Site;

  @Prop({
    required: true,
    type: Object
  })
  theme: Theme;
}



